.posenet {
  position: absolute;
  left: 85%;
  width: 10vw;
  height: 20vh;
  border-radius: 50%;
  margin: 4vh;
  box-shadow: 0px 0px 100px 20px #fffd75d7;
  -webkit-animation: ray_anim 20s infinite;
          animation: ray_anim 20s infinite;
}
.posenet:hover {
  -webkit-animation: posenet 3s 1;
          animation: posenet 3s 1;
}

@-webkit-keyframes ray_anim {
  0% {
    box-shadow: 0px 0px 100px 60px #ffffff;
  }
  20% {
    box-shadow: 0px 0vh 100px 30px #fffd99;
  }
  40% {
    box-shadow: 0px 0vh 100px 40px #fffd50;
  }
  60% {
    box-shadow: 0px 0vh 100px 50px #fffd75;
  }
  80% {
    box-shadow: 0px 0vh 100px 60px #fffd99;
  }
  100% {
    box-shadow: 0px 0px 100px 20px #fffd75d7;
  }
}

@keyframes ray_anim {
  0% {
    box-shadow: 0px 0px 100px 60px #ffffff;
  }
  20% {
    box-shadow: 0px 0vh 100px 30px #fffd99;
  }
  40% {
    box-shadow: 0px 0vh 100px 40px #fffd50;
  }
  60% {
    box-shadow: 0px 0vh 100px 50px #fffd75;
  }
  80% {
    box-shadow: 0px 0vh 100px 60px #fffd99;
  }
  100% {
    box-shadow: 0px 0px 100px 20px #fffd75d7;
  }
}
