.posenet {
  position: absolute;
  left: 85%;
  width: 10vw;
  height: 20vh;
  border-radius: 50%;
  margin: 4vh;
  box-shadow: 0px 0px 100px 20px #fffd75d7;
  animation: ray_anim 20s infinite;
}
.posenet:hover {
  animation: posenet 3s 1;
}

@keyframes ray_anim {
  0% {
    box-shadow: 0px 0px 100px 60px #ffffff;
  }
  20% {
    box-shadow: 0px 0vh 100px 30px #fffd99;
  }
  40% {
    box-shadow: 0px 0vh 100px 40px #fffd50;
  }
  60% {
    box-shadow: 0px 0vh 100px 50px #fffd75;
  }
  80% {
    box-shadow: 0px 0vh 100px 60px #fffd99;
  }
  100% {
    box-shadow: 0px 0px 100px 20px #fffd75d7;
  }
}
@keyframes posenet {
  0% {
  }
  50% {
    border-radius: 10px;
  }
  100% {
    border-radius: 8px;
  }
}
@media only screen and (max-width: 480px) {
  .posenet {
    position: absolute;
    left: 20%;
    width: 40vw;
    height: 20vh;
    border-radius: 50%;
    margin: 4vh;
    box-shadow: 0px 0px 100px 20px #fffd75d7;
    animation: ray_anim 20s infinite;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  .posenet {
    position: absolute;
    left: 20%;
    width: 40vw;
    height: 30vh;
    border-radius: 50%;
    margin: 4vh;
    box-shadow: 0px 0px 100px 20px #fffd75d7;
    animation: ray_anim 20s infinite;
  }
}
@media screen and (min-width: 769px) and (max-width: 1024px) {
}
